<template>
    <div class="app-container">
        <div class="wraper">
            <div class="left">
                <div class="header-pic"><img src="./img/resume.jpg" alt=""></div>
                <div class="base-data">
                    <div><span class="fa fa-calendar"></span><span>{{age}}</span></div>
                    <div><span class="fa fa-address-card "></span><span></span></div>
                    <div><span class="fa fa-map-pin"></span><span>{{address}}</span></div>
                    <div><span class="fa fa-calendar-check-o"></span><span>2 </span></div>
                    <div><span class="fa fa-phone"></span><span v-text="phone"></span></div>
                    <div><span class="fa fa-envelope"></span><span v-text="email"></span></div>
                </div>
                <div class="skill">
                    <div class="title">
                        <span class="fa fa-wrench"></span>
                        <span></span>
                    </div>
                    <div class="skill-list">
                        <div class="list" v-for="skill in skills" v-bind:key="skill.name">
                            <div class="name" v-text="skill.name"></div>
                            <div class="context">
                                <div class="progress"><div class="fill-bar" :style="{ width: `${skill.len}%`}"></div><div class="empty-bar" :style="{ width: `${100 - skill.len}%`}"></div></div>
                                <div class="skilled" v-text="skill.skilled"></div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="right">
                <div class="header" v-text="name"></div>
                <div class="overview" v-text="overview"></div>
                <div class="education">
                    <div class="title">
                        <span class="fa fa-graduation-cap"></span>
                        <span class="text"></span>
                    </div>
                    <div class="content">
                        <div class="grad-wrap" v-for="edu in educations" v-bind:key="edu.school">
                            <div class="lists">
                                <span class="custom" v-text="edu.date"></span>
                                <span class="custom" v-text="edu.school"></span>
                                <span class="custom" v-text="edu.subject"></span>
                                <span class="custom" v-text="edu.top"></span>
                            </div>
                            <div class="grad-subject">
                                <span></span>
                                <span v-text="edu.detail"></span>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="work">
                    <div class="title">
                        <span class="fa fa-keyboard-o"></span>
                        <span class="text"></span>
                    </div>
                    <div class="content">
                        <div class="lists" v-for="work in works" v-bind:key="work.company">
                            <span class="default" v-text="work.date"></span>
                            <span class="default" v-text="work.company"></span>
                            <span class="default" v-text="work.position"></span>
                        </div>
                    </div>
                    <div class="detail">
                        <div class="title"></div>
                        <div class="detail-lists">
                            <div class="detail-list" v-for="detail in detailLists" v-bind:key="detail.title">
                                <span class="pot"></span>
                                <div class="text">
                                    <div class="detail-title">
                                        <span v-text="detail.title"></span>
                                        <span v-text="detail.manager"></span>
                                    </div>
                                    <div class="detail-content" v-text="detail.detail">
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="comment-self">
                    <div class="title">
                        <span class="fa fa-pencil-square-o"></span>
                        <span class="text"></span>
                    </div>
                    <div class="comment-wrap">
                        <div class="comment" v-for="cmt in commetMyselfs" v-bind:key="cmt">
                            <p v-text="cmt"></p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'resume',
        data () {
            return {
                name: '',
                age: 28,
                address: '',
                phone: '18810665837',
                email: '1025263994@qq.com',
                overview: 'web~~',
                skills: [{
                    name: 'es6+ & node',
                    len: 90,
                    skilled: '',
                }, {
                    name: 'css',
                    len: 90,
                    skilled: '',
                }, {
                    name: 'vue & react',
                    len: 85,
                    skilled: '',
                }, {
                    name: 'koa2',
                    len: 85,
                    skilled: '',
                }, {
                    name: 'webpack',
                    len: 80,
                    skilled: '',
                }, {
                    name: '',
                    len: 80,
                    skilled: '',
                }, {
                    name: 'WebAssembly',
                    len: 50,
                    skilled: '',
                }],
                educations: [{
                    date: '2010.09 - 2014.06',
                    school: '',
                    subject: '',
                    top: '10%',
                    detail: 'FPGA',
                }, {
                    date: '2014.09 - 2017.03',
                    school: '',
                    subject: '',
                    top: '30%',
                    detail: ' HEVC ',
                }],
                works: [{
                    date: '2016.11 - 2018.3',
                    company: '',
                    position: 'web',
                }, {
                    date: '2018.4 - ',
                    company: '',
                    position: 'web',
                }],
                detailLists: [{
                    title: ' koa2  node  - monking ',
                    manager: '',
                    detail: ' node  node monking  MVC  aop  github https://github.com/chenhebing monking  chrome  monking ',
                }, {
                    title: 'js bridge',
                    manager: '',
                    detail: ' H5  native  native  js  js bridge  api js bridge  npm  api  native  mapping  npm ',
                }],
                commetMyselfs: [
                    '',
                    ' web ',
                    '',
                ],
            };
        },
    };
</script>

<style lang="less">
    @import "./styles/font-awesome.min.css";
    @import "./styles/main.less";
    .app-container {
        width: 100%;
        height: 1140px;
        /*background-color: #1ebf92;*/
        display: flex;
        justify-content: center;
        .wraper {
            width: 820px;
            min-width: 820px;
            /*margin-top: 75px;*/
            display: flex;
            .left {
                width: 270px;
                background-color: #0b70bd;
                .header-pic {
                    margin-top: 60px;
                    text-align: center;
                    img {
                        border: 4px solid #e5e5e5;
                        width: 120px;
                        height: 150px;
                    }
                }
                .base-data {
                    color: white;
                    font-family: Microsoft YaHei;
                    font-size: 16px;
                    margin: 30px 0 0 30px;
                    overflow: hidden;
                    div {
                        display: flex;
                        align-items: center;
                        margin-bottom: 10px;
                        .fa {
                            margin-right: 10px;
                            font-size: 14px;
                            display: block;
                            width: 20px;
                            text-align: center;
                        }
                    }
                }
                .skill {
                    margin-top: 30px;
                    margin-left: 30px;
                    .title {
                        color: white;
                        font-size: 18px;
                        display: flex;
                        align-items: center;
                        .fa {
                            font-size: 24px;
                            margin-right: 10px;
                        }
                    }
                    .skill-list {
                        margin-top: 30px;
                        .list {
                            margin-bottom: 15px;
                            color: white;
                            .name {
                                font-size: 18px;
                                line-height: 30px;
                            }
                            .context {
                                display: flex;
                                justify-content: space-between;
                                align-items: center;
                                .progress {
                                    height: 10px;
                                    width: 150px;
                                    display: flex;
                                    overflow: hidden;
                                    border-radius: 2px;
                                    .fill-bar {
                                        background-color: #329cec;
                                    }
                                    .empty-bar {
                                        background-color: white;
                                    }
                                }
                                .skilled {
                                    margin-right: 30px;
                                    font-size: 16px;
                                }
                            }
                        }
                    }
                }
            }
            .right {
                flex: 1;
                color: #333333;
                background-color: white;
                padding-left: 30px;
                padding-right: 30px;
                font-family: Microsoft YaHei;
                .header {
                    margin-top: 30px;
                    font-size: 30px;
                }
                .overview {
                    margin-top: 10px;
                    color: #878686;
                    font-size: 14px;
                }
                .grad-wrap {
                    margin-bottom: 10px;
                    .grad-subject {
                        color: #878686;
                        font-size: 14px;
                        padding-left: 20px;
                    }
                }
                .education, .work, .comment-self {
                    margin-top: 30px;
                    .title {
                        display: flex;
                        align-items: flex-end;
                        .fa {
                            margin-right: 10px;
                            display: block;
                            width: 30px;
                            height: 30px;
                            overflow: hidden;
                            border-radius: 15px;
                            color: white;
                            background-color: #0b70bd;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                        }
                        .text {
                            color: #0b70bd;
                            flex: 1;
                            font-size: 18px;
                            line-height: 30px;
                            border-bottom: 1px solid #0b70bd;
                        }
                    }
                    .content {
                        margin-top: 20px;
                        .lists {
                            display: flex;
                            justify-content: space-between;
                            align-items: center;
                            margin-bottom: 10px;
                            span:first-child {
                                text-align: left;
                                margin-left: 10px;
                            }
                            span:last-child {
                                text-align: right;
                                margin-right: 10px;
                            }
                            .custom {
                                flex-grow: 1;
                                text-align: center;
                                &:nth-child(1) {
                                    width: 30%;
                                }
                                &:nth-child(2) {
                                    width: 25%;
                                }
                                &:nth-child(3) {
                                    width: 30%;
                                }
                                &:nth-child(4) {
                                    width: 15%;
                                }
                            }
                            .default {
                                flex: 1;
                                text-align: center;
                            }
                        }
                    }
                    .detail {
                        margin-left: 10px;
                        margin-top: 15px;
                        font-size: 14px;
                        line-height: 22px;
                        .title {
                            color: #333;
                            font-size: 16px;
                        }
                        .detail-list {
                            display: flex;
                            max-height: 100%;
                            margin-top: 6px;
                            .pot {
                                display: block;
                                width: 4px;
                                height: 4px;
                                background-color: #878686;
                                border-radius: 2px;
                                margin-right: 10px;
                                margin-top: 10px;
                            }
                            .text {
                                flex: 1;
                                .detail-title {
                                    color: #666;
                                    margin-bottom: 4px;
                                    span:first-child {
                                        margin-right: 10px;
                                        font-weight: bold;
                                    }
                                }
                                .detail-content {
                                    color: #878686;
                                }
                            }
                        }
                    }
                    .comment-wrap {
                        margin: 20px 0 0 10px;
                        .comment {
                            color: #6f6f6f;
                            font-size: 14px;
                            line-height: 22px;
                            margin-bottom: 8px;
                            p {
                                text-indent: 28px;
                                text-align: justify;
                            }
                        }
                    }
                }
            }
        }
    }
    body {
        margin: 0;
    }
</style>



